
import { useState, useEffect, useMemo } from 'react';
import React from 'react';
import ReactPaginate from 'react-paginate';
import productsData from '../productsData.js';

function Pagination(props) {
    const [pageCount, setPageCount] = useState(4);

    const [componentState, setComponentState] = useState({
        offset: 0,
        data: [],
        perPage: 6,
        currentPage: 0
    });
    const [postData, setPostData] = useState('');
    const[paginationCount, setPaginationCount] = useState(1);
    

    function onCartClick(e){
        // props.addToCart(names, image, price, id);
        //console.log('cartClicked');
       // console.log(e.target.name);
        let mId = e.target.name;
        let datass = props.data
        const objectData = [datass[mId].name, datass[mId].image, datass[mId].price, datass[mId].id]
        props.addToCart2(objectData[0], objectData[1].src, objectData[2], objectData[3]);
    }

    const moses = props.addToCart;
    function receivedData() { 
                const data = productsData.products;
                const slice = data.slice(componentState.offset, componentState.offset + componentState.perPage)
                const postDatas = slice.map((pd) => <div className='col-sm-4'>
                    <img src={pd.image.src} alt={pd.image.alt} className='products-catalog-image'/>
                    <p className='product-bestseller' style={pd.bestseller? {display: 'block'} : {display: 'none'}}>Bestseller</p>
                    <button type="button" className="btn btn-dark product-catalog-button" name={pd.id} onClick={onCartClick}>Add to Cart</button>
                    <p className='products-catelog-catagory'>{pd.category}</p>
                    <h4 className='products-catalog-title'>{pd.name}</h4>
                    <h5 className='products-catalog-price'>{pd.currency} {pd.price}</h5>
                </div>)

                setPostData(postDatas)
  
                setComponentState({
                    pageCount: Math.ceil(data.length / setComponentState.perPage),
                   
                    postDatas
                })
    }

    function handlePageClick(e) {
        const selectedPage = e.selected;
        const offset = selectedPage * 6;
  
        setComponentState({
            currentPage: selectedPage,
            offset: offset
        }, () => {
            receivedData();
        });
  
    };

    const status = props.categoryStatus;

    
    function increasePageCount(){
        if(paginationCount < 4) {
        setPaginationCount(paginationCount + 1);
        }
    }

    function decreasePageCount(){
        if(paginationCount < 1 ){
        setPaginationCount(paginationCount - 1);
        } else {
            setPaginationCount(1);
        }
    }

   useEffect(()=>{
       receivedData();
   }, [])

  



    return(
        <div className='pagination-component'>
            <div className='row'>
                {postData}
            </div>
            <div className='pagination-nav-body col-sm-3'>
            {/* <ReactPaginate
                    previousLabel={<img src="https://img.icons8.com/ios-glyphs/20/000000/chevron-left.png"/>}
                    nextLabel={<img src="https://img.icons8.com/ios-glyphs/20/000000/chevron-right.png"/>}
                    breakLabel={"..."}
                    breakClassName={"break-me"}
                    pageCount={pageCount}
                    marginPagesDisplayed={2}
                    pageRangeDisplayed={5}
                    onPageChange={handlePageClick}
                    containerClassName={"pagination"}
                    subContainerClassName={"pages pagination"}
                    activeClassName={"active"}/>
            </div> */}

            <div className='pagination-nav-body col-sm-3' style={{display: 'flex'}}>
                <p style={{marginRight: '20px'}}><img src="https://img.icons8.com/ios-glyphs/20/000000/chevron-left.png" onClick={decreasePageCount}/></p>
                <p className={paginationCount == 1 ? 'page-active' : ''} style={{marginRight: '20px'}}>1</p>
                <p className={paginationCount == 2 ? 'page-active' : ''} style={{marginRight: '20px'}}>2</p>
                <p className={paginationCount == 3 ? 'page-active' : ''} style={{marginRight: '20px'}}>3</p>
                <p className={paginationCount == 4 ? 'page-active' : ''} style={{marginRight: '20px'}}>4</p>
                <p><img src="https://img.icons8.com/ios-glyphs/20/000000/chevron-right.png" onClick={increasePageCount}/></p>
                </div>
            </div>
        </div>
    )
}

export default Pagination;