import logo from './logo.svg';
import shoppingCart from './assets/img/shopping-cart.png';
import closeIcon from './assets/img/close-icon.png';
import productsData from './productsData.js';
import { useState, useEffect } from 'react';
import ReactPaginate from 'react-paginate';
import Pagination from './components/Pagination';
import './App.css';

function App() {
  const [cart, setCart] = useState({ cartItemNumber: 0, cartItemName: '', cartItemImage: ''});
  const [cartToggle, setCartToggle] = useState({ count: 0, cartOpened: false });

  const [productsItem, setProductItem] = useState(productsData.products);
  const [newProductItem, setNewProductItem] = useState(productsData.products);

  const [cartItems, setCartItems] = useState([]);

  const [car, setCar] = useState([]);

  const [car2, setCar2] = useState([]);

  const [count, setCount] = useState(1);

  const [checkboxToggle, setCheckboxToggle] = useState({checkbox: 0, checkboxOpened: false});

  const [windowWidth, setWindowWith] = useState(window.innerWidth);


const [categoryCount, setCategoryCount] = useState(0);


  function openCart() {
    if (cartToggle.count == 0) {
      setCartToggle({ count: 1, cartOpened: true })
    }
  }
  function closeCart() {
    if (cartToggle.count == 1) {
      setCartToggle({ count: 0, cartOpened: false })
    }
  }

  function openCheckedbox() {
    if (checkboxToggle.checkbox == 0) {
      setCheckboxToggle({ checkbox: 1, checkboxOpened: true});
    }
  }

  function closeCheckedbox() {
    if (checkboxToggle.checkbox == 1) {
      setCheckboxToggle({ checkbox: 0, checkboxOpened: false});
    }
  }
  // function addToCart2(objectData){
  //   setCar2(objectData);
  //   //console.log(objectData)
  // }

  function addToCart(name, image, price, id) {
   
    setCart({
      cartItemNumber: cart.cartItemNumber + 1,
      cartItemName: name,
      cartItemImage: image
    });
    console.log(id);

    // function add() {
    //   setCartItems([...cartItems], [cart.cartItemName, cart.cartItemImage]);
    // }
    // add();
    // this.setState(prevState => ({
    //   myArray: [...prevState.myArray, "new value"]
    // }))
    function add() {
     
        // let newArray = {productsItem[id - 1].name,productsItem[id - 1].image, productsItem[id - 1].price]
        setCartItems(prevCartItems => ([...prevCartItems, cart.cartItemName, cart.cartItemImage]));
        setCar(prevCar => ([...prevCar, { name: cart.cartItemName, image: cart.cartItemImage, price: price, id: id }]));
      
    }
    add();
  }

  function addToCart2(name, image, price, id){
    const objectData = image
    console.log(objectData);
    setCart({
      cartItemNumber: cart.cartItemNumber + 1,
      cartItemName: name,
      cartItemImage: image
    });
    setCar2(objectData);
    function addagain() {
        setCartItems(prevCartItems => ([...prevCartItems, cart.cartItemName, cart.cartItemImage]));
        setCar(prevCar => ([...prevCar, {name: name, image: image, price: price, id: id }]))
    }
    addagain();
    setCount(count + 1);
  }

  function clearCart() {
    setCart({
      cartItemNumber: 0,
      cartItemName: '',
      cartItemImage: ''
    });
    setCar([]);
  }

  const handleResize = (e) => {
    setWindowWith(window.innerWidth);
   };
  
  useEffect(() => {
    window.addEventListener("resize", handleResize);
  });

  const [categoryStatus, setCategoryStatus] = useState('')
  function filterData(e){
    e.preventDefault();

    let categorySelected = e.target.name;
    const filteredDataNamed = newProductItem.filter(product => product.category == categorySelected);

    if (filteredDataNamed.length > 0 && filteredDataNamed.length < 6) {
      setCategoryStatus(categorySelected);
      console.log('filterData');
      setCategoryCount(categoryCount + 1);
      if(categoryCount == 1) {

          setNewProductItem(filteredDataNamed);
    
          setCategoryStatus(categorySelected);
    
          console.log(categoryStatus);
          console.log(newProductItem)
        }
    }

   
    // if(filteredDataNamed.length == 5) {

    //   setNewProductItem(filteredDataNamed);

    //   setCategoryStatus(categorySelected);

    //   console.log('filterData');
    //   console.log(categoryStatus);
    //   console.log(newProductItem)
    // }
    
    
  }
  return (
    <div className="App">

      <div className='app-header'>
        <h3 className='logo-name'>Bejamas_</h3>
        <img src={shoppingCart} style={count < 1 ? { top: '25px' } : { top: '16px' }} onClick={openCart} />

        <h5 className='cart-number' style={count < 1 ? { display: 'none' } : { display: 'block' }}>{count > 0 ? car.length + 1 : '0'}</h5>
        <hr></hr>
      </div>

      <div className='shopping-cart-item' style={cartToggle.cartOpened ? { display: 'block' } : { display: 'none' }}>
        <img className='close-icon' src={closeIcon} onClick={closeCart} />
        <div className='shopping-cart-item-details'>
          {car.map((person, index) => (
            <div className='row' key={person.id}>
              <div className='col-7'>
                <p className='shopping-cart-item-name'>{person.name}</p>
                <p className='shopping-cart-item-price'>{person.price} USD</p>
              </div>
              <img src={person.image} className='col-5' />
            </div>
          ))}
          <button type="button" className="btn btn-dark shopping-cart-button" onClick={clearCart}>CLEAR</button>
        </div>
      </div>

      <div className='featured-product'>
        <h3 className='featured-product-name'>{productsItem[4].name}</h3>
        <button type="button" className="btn btn-dark featured-product-button" onClick={() => addToCart2(productsItem[4].name, productsItem[4].image.src, productsItem[4].price, productsItem[4].id)}>Add to Cart</button>
        <div className='featured-product-image-and-tag'>
        <img src={productsItem[4].image.src} className='featured-product-image'/>
        <p className='featured-product-photo-of-the-day'>Photo of the day</p>
        </div>
        <div className='row featured-product-sub-section'>
          <div className='col-sm-8 featured-product-about'>
            <h3>About the {productsItem[4].name}</h3>
            <h5>{productsItem[4].category}</h5>
            <p className='featured-product-paragraph-one'>{productsItem[4].details.description}</p>
            <p className='featured-product-paragraph-two'>{productsItem[4].details.descriptiontwo}</p>
          </div>
          <div className='col-sm-4 featured-product-people-buy'>
            <h3>People also buy</h3>
            <div className='row'>
              <div className='col-4'>
                <img src={productsItem[0].image.src}/>
              </div>
              <div className='col-4'>
              <img src={productsItem[1].image.src}/>
              </div>
              <div className='col-4'>
              <img src={productsItem[0].image.src}/>
              </div>
            </div>
            <h3>Details</h3>
            <p>Size: {productsItem[4].details.dimmentions.width} by {productsItem[4].details.dimmentions.height}</p>
            <p>Size: {productsItem[4].details.size}</p>
          </div>
        </div>
        <hr></hr>
      </div>
      <div className='main-page'>
        <h3 className='main-page-h3-1'>Photography / <span>Premium Photos</span></h3>
        <h3 className='main-page-h3-2 main-page-h3-2-desktop'><span><img src="https://img.icons8.com/pastel-glyph/24/000000/sorting-arrows--v2.png"/> Sort By</span> Price <img src="https://img.icons8.com/ios/24/000000/expand-arrow--v1.png"/></h3>
        <h3 className='main-page-h3-2 main-page-h3-2-mobile'><img src="https://img.icons8.com/ios-glyphs/50/000000/content.png" onClick={openCheckedbox}/></h3>
        <div className='row'>
          <div className='col-sm-2 mainpage-filter-menu' style={windowWidth > 600 ? { display: 'block' } : checkboxToggle.checkboxOpened ? { display: 'block' } : { display: 'none' }}>
            <h5>Category</h5>
            <div className='filter-and-close'>
              <h5>Filter</h5>
              <img src={closeIcon} onClick={closeCheckedbox}/>
            </div>
            <div className='category-input'>
              <input type='checkbox' className='category-checkbox' name='people' value='people'/> <p>People</p>
            </div>
            <div className='category-input'>
              <input type='checkbox' className='category-checkbox' name='premuim'/> <p>Premuim</p>
            </div>
            <div className='category-input'>
              <input type='checkbox' className='category-checkbox' name='pets'/> <p>Pets</p>
            </div>
            <div className='category-input'>
              <input type='checkbox' className='category-checkbox' name='food'/> <p>Food</p>
            </div>
            <div className='category-input'>
              <input type='checkbox' className='category-checkbox' name='landmarks'/> <p>Landmarks</p>
            </div>
            <div className='category-input'>
              <input type='checkbox' className='category-checkbox' name='cities'/> <p>Cities</p>
            </div>
            <div className='category-input'>
              <input type='checkbox' className='category-checkbox' name='nature'/> <p>Nature</p>
            </div>
            <hr></hr>
            <h5>Price Range</h5>
            <div className='category-input'>
              <input type='checkbox' className='category-checkbox' name='20'/> <p>Lower than $20</p>
            </div>
            <div className='category-input'>
              <input type='checkbox' className='category-checkbox' name='100'/> <p>$20 - $100</p>
            </div>
            <div className='category-input'>
              <input type='checkbox' className='category-checkbox' name='200'/> <p>$100 - $200</p>
            </div>
            <div className='category-input'>
              <input type='checkbox' className='category-checkbox' name='201'/> <p>More than $200</p>
            </div>
            <div className='filter-button'>
              <button type="button" className="btn filter-clear-button">CLEAR</button>
              <button type="button" className="btn filter-save-button">SAVE</button>
            </div>
          </div>
          <div className='col-8 mainpage-products'>
            <Pagination data={newProductItem} addToCart2={addToCart2} categoryStatus={categoryStatus}/>
          </div>
        </div>
      </div>
    </div >
  );
}

export default App;
